/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect } from "react";
import { Box, Modal, Backdrop, TextField, Checkbox, InputAdornment, FormControlLabel, Select, FormControl, MenuItem, InputLabel } from '@mui/material';
import { ModalContext } from "../content/TableProfile";

import { dayjs } from "./Date";

import { toast } from "react-toastify";

const fetch = require('node-fetch');

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    border: '2px solid #8DC53F',
    overflow: 'scroll',
    maxWidth: '90%',
};


export default function MiningToolsModal() {
    const { showModalMiningTools, setShowModalMiningTools } = useContext(ModalContext);

    const handleChange = (event) => {

        const { name, value } = event.target;
        setShowModalMiningTools((prevTime) => {
            return {
                ...prevTime,
                [name]: value
            };
        });
    };

    const handleChangeMineBucket = (event) => {
        const { name, value } = event.target;
        setShowModalMiningTools((prevKey) => {
            return {
                ...prevKey,
                [name]: value,
                "status": event.target.checked
            };
        });
    };

    const PushTime = async () => {

        const options = {
            method: 'POST',
            headers: {
                'Authorization': "Bearer " + localStorage.getItem('token'),
                "Content-type": "application/json",
                "Accept": "application/json",
            },
            body: JSON.stringify({
                account: showModalMiningTools.wax,
                actions: {
                    start: Number(showModalMiningTools.start),
                    stop: Number(showModalMiningTools.stop),
                },
            })
        };

        const SendPushTransaction = new Promise(async (resolve, rejected) => {
            await fetch("/working", options)
                .then(res => res.json())
                .then(json => resolve(json))
                .catch(err => rejected(err));
        });

        toast.promise(
            SendPushTransaction,
            {
                pending: 'Promise is pending',
                success: {
                    render({ data }) { return `Succeed Setup` },
                },
                error: {
                    render({ data }) {
                        // When the promise reject, data will contains the error
                        return `Failed Setup`
                    }
                }
            }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
        )
    }

    const PushMineBucket = async () => {

        const options = {
            method: 'POST',
            headers: {
                'Authorization': "Bearer " + localStorage.getItem('token'),
                "Content-type": "application/json",
                "Accept": "application/json",
            },
            body: JSON.stringify({
                account: showModalMiningTools.wax,
                actions: {
                    mine_bucket: {
                        status: showModalMiningTools.status,
                        tlm: showModalMiningTools.mine_bucket,
                        time_out: showModalMiningTools.time_out
                    },
                },
            })
        };

        const SendPushTransaction = new Promise(async (resolve, rejected) => {
            await fetch("/mine_bucket", options)
                .then(res => res.json())
                .then(json => resolve(json))
                .catch(err => rejected(err));
        });

        toast.promise(
            SendPushTransaction,
            {
                pending: 'Promise is pending',
                success: {
                    render({ data }) { return `Succeed Setup` },
                },
                error: {
                    render({ data }) {
                        // When the promise reject, data will contains the error
                        return `Failed Setup`
                    }
                }
            }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
        )
    }


    const getMineBucket = async (id) => {
        await fetch(`/mine_bucket/${id}`, {
            method: 'GET', headers: { 'Authorization': "Bearer " + localStorage.getItem('token'), },
        }).then(res => res.json())
            .then(json => {
                if (json.mine_bucket) {
                    setShowModalMiningTools((prevTime) => {
                        return {
                            ...prevTime,
                            mine_bucket: json.mine_bucket.tlm,
                            status: json.mine_bucket.status,
                            time_out: json.mine_bucket.time_out
                        };
                    });
                }
            })
            .catch(err => console.error(err.message));
    }

    const getMineBucketPlanet = async (planet) => {
        const url = 'https://chain.wax.io/v1/chain/get_table_rows';

        const options = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                "json": true,
                "code": "m.federation",
                "scope": planet,
                "table": "state3",
                "reverse": false,
                "show_payer": false
            })
        };

        for (let index = 0; index < 10; index++) {
            await fetch(url, options)
                .then(res => res.json())
                .then(json => setResPlanet(json))
                .catch(err => console.error('error:' + err));
        }
    }

    useEffect(() => {
        if (showModalMiningTools.show) {
            getMineBucket(showModalMiningTools.wax)
        }
    }, [showModalMiningTools.show])
    const [planet, setPlanet] = React.useState("veles.world");
    const [resPlanet, setResPlanet] = React.useState({
        "rows": [
            {
                "last_fill_time": "2022-01-10T09:50:26.500",
                "fill_rate": "0.0",
                "bucket_total": "0.0000 TLM",
                "mine_bucket": "0.0000 TLM"
            }
        ],
        "more": false,
        "next_key": ""
    });
    const handleChangePlanet = (event) => {
        setPlanet(event.target.value);
    };
    return (
        <>
            <Modal
                open={showModalMiningTools.show}
                onClose={() => setShowModalMiningTools({ ...showModalMiningTools, show: !showModalMiningTools.show })}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Box sx={style} className={"text-gray-darker dark:text-gray-light bg-gray-lighter dark:bg-gray-darker outline-none p-3 rounded-xl font-semibold w-full md:w-3/12"}>
                    <div className="relative justify-center items-center ">
                        <div className="flex flex-col w-full rounded-lg bg-green mt-2 p-2 space-y-2">
                            <p className="text-left text-lg text-gray-darker">Setup Mining</p>
                            <div className="flex flex-col w-full rounded-lg border-2 border-green bg-gray-lighter dark:bg-gray-darker p-2 space-y-2">
                                <div className="flex flex-col space-y-3">
                                    <div className="flex flex-row space-x-3" style={{ marginTop: 3 }}>
                                        <TextField
                                            label="Work Script"
                                            fullWidth={true}
                                            type="number"
                                            value={showModalMiningTools.start}
                                            defaultValue={0}
                                            size="small"
                                            name="start"
                                            onChange={handleChange}
                                            InputLabelProps={{ shrink: true, }}
                                            InputProps={{ endAdornment: <InputAdornment position="end">Hour</InputAdornment>, inputProps: { min: "0", max: "24", step: "1" } }}
                                        />
                                        <TextField
                                            label="Standby Script"
                                            fullWidth={true}
                                            type="number"
                                            defaultValue={0}
                                            value={showModalMiningTools.stop}
                                            size="small"
                                            name="stop"
                                            onChange={handleChange}
                                            InputLabelProps={{ shrink: true, }}
                                            InputProps={{ endAdornment: <InputAdornment position="end">Hour</InputAdornment>, inputProps: { min: "0", max: "24", step: "1" } }}
                                        />
                                    </div>

                                    <div className="grid grid-cols-2 gap-2 text-xs">
                                        <span className="text-left">Work Time</span>
                                        <span className="bg-green rounded-lg text-right text-gray-darkest px-2"> {dayjs.utc(showModalMiningTools.strStart).local().format("YYYY-MM-DD HH:mm:ss")}</span>
                                        <span className="text-left">Standby Time</span>
                                        <span className="bg-warning rounded-lg text-right text-gray-darkest px-2"> {dayjs.utc(showModalMiningTools.strStop).local().format("YYYY-MM-DD HH:mm:ss")}</span>
                                    </div>
                                    <button className="font-bold h-10 outline-none uppercase rounded-lg py-1.5 px-6 text-xs text-white bg-green hover:text-gray-darker" onClick={() => PushTime()}>Save</button>
                                </div>
                            </div>

                            <div className="flex flex-col w-full rounded-lg border-2 border-green bg-gray-lighter dark:bg-gray-darker p-2 space-y-2">
                                <div className="flex flex-row -mt-2 ">
                                    <FormControlLabel
                                        control={<Checkbox
                                            name="status"
                                            checked={showModalMiningTools.status}
                                            onChange={handleChangeMineBucket}
                                            sx={{ '& .MuiSvgIcon-root': { fontSize: 28 } }}
                                        />}
                                        label="Mine Bucket"
                                        labelPlacement="end"

                                    />
                                </div>
                                <div className="flex flex-col space-y-2 md:flex-row md:space-x-2 md:space-y-0" style={{ marginTop: 3 }}>
                                    <TextField
                                        label="Setup mine bucket / TLM"
                                        type="number"
                                        InputLabelProps={{ shrink: true, }}
                                        variant="outlined"
                                        fullWidth={true}
                                        size="small"
                                        name="mine_bucket"
                                        value={showModalMiningTools.mine_bucket}
                                        onChange={({ target: { value } }) => { if (/^\d+(\.\d{0,4})?$/.test(value)) { handleChange({ target: { name: "mine_bucket", value: value } }); } }}
                                        InputProps={{ inputProps: { min: "0", max: "10", step: "0.05" } }}
                                    />

                                    <TextField
                                        label="Setup time out / Sec."
                                        type="number"
                                        InputLabelProps={{ shrink: true, }}
                                        variant="outlined"
                                        fullWidth={true}
                                        size="small"
                                        name="time_out"
                                        defaultValue={0}
                                        value={showModalMiningTools.time_out}
                                        onChange={handleChange}
                                        InputProps={{ inputProps: { min: "15", max: "300", step: "1", lang: "en-US" } }}
                                    />

                                </div>
                                <button className="font-bold outline-none uppercase w-full rounded-lg h-10 px-6 text-xs text-white bg-green hover:text-gray-darker" onClick={() => PushMineBucket()}>Save</button>
                                <div className="flex flex-col space-y-2 md:flex-row md:space-x-2 md:space-y-0">
                                    <FormControl fullWidth size={"small"} >
                                        <InputLabel id="demo-simple-select-label">Planet</InputLabel>
                                        <Select
                                            value={planet}
                                            label="Planet"
                                            onChange={handleChangePlanet}
                                        >
                                            <MenuItem value={"veles.world"}>Veles</MenuItem>
                                            <MenuItem value={"kavian.world"}>Kavian</MenuItem>
                                            <MenuItem value={"eyeke.world"}>Eyeke</MenuItem>
                                            <MenuItem value={"naron.world"}>Naron</MenuItem>
                                            <MenuItem value={"neri.world"}>Neri</MenuItem>
                                            <MenuItem value={"magor.world"}>Magor</MenuItem>
                                        </Select>
                                    </FormControl>
                                    <button className="font-bold outline-none uppercase w-full rounded-lg h-10 px-6 text-xs text-white bg-primary" onClick={() => getMineBucketPlanet(planet)}>CHECK</button>
                                </div>
                                <div className="flex flex-col space-y-2 justify-center  text-xs">
                                    <div>Bucket Total : <label className=" font-bold text-info-dark">{resPlanet.rows.length > 0 ? resPlanet.rows[0].bucket_total : null}</label>  / sec.</div>
                                    <div>Mine Bucket : <label className=" font-bold text-success-dark">{resPlanet.rows.length > 0 ? resPlanet.rows[0].mine_bucket : null}</label> / sec.</div>
                                </div>
                            </div>
                        </div>

                    </div>
                </Box>
            </Modal>
        </>
    );
}

